@mixin flex-grid-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

@mixin flex-grid-column($divide: 1, $multiply: 1) {
    position: relative;
    min-height: 1px;
    padding: 0 15px;
    margin: 15px 0;
    width: (100% / $divide) * $multiply;
}

@mixin flex-grid-vertical-row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}


